.skeleton{display:block;background-color:rgba(0,0,0,.11);animation:Skeleton-keyframes-pulse 1s ease-in-out .5s infinite;position:relative;color:linear-gradient(90deg, #eee, #f5f5f5, #eee)}.skeleton__overlay{position:absolute;top:0;left:0;right:0;bottom:0;background-color:#fff}.skeleton-circle{border-radius:100%}.skeleton-text{height:auto;transform:scale(1, 0.6);margin-top:0;border-radius:4px;margin-bottom:0;transform-origin:0 60%}.skeleton-text:empty:before{content:" "}@keyframes Skeleton-keyframes-pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}